il, rango de edad, intereses y la manera como el objeto se relaciona con el que hacer de la empresa. Para ilustrarlo, pensemos en un evento de informática al que acudirán ingenieros: los objetos promocionales deberían estar pensados para ellos, y por lo tanto, pueden ser accesorios tecnológicos, "),_c('i',[_vm._v("pads")]),_vm._v(" para el ratón del computador, forros para el portátil, etc. ")]),_c('p',[_vm._v("Para evitar errores, también se pueden hacer encuestas en línea a los clientes con el fin de conocer mejor sus preferencias. Algunas ideas de regalos pueden ser:")]),_c('ul',{staticClass:"lista-ul--color"},[_c('li',[_c('i',{staticClass:"fas fa-chevron-right color-vineta1"}),_c('p',{staticClass:"mb-0"},[_vm._v("Regalos de patrocinadores. Es una labor importante conseguir empresas que sirvan de patrocinadoras para el evento. La mayoría aporta sus detalles para que sean entregados a los clientes. Esto es un beneficio en doble vía, ya que el organizador puede obsequiar a cada asistente una caja con su marca y alguno de sus productos, junto con varios regalos de los patrocinadores, así todos se dan a conocer o afianzan su presencia entre los asistentes.")])]),_c('li',[_c('i',{staticClass:"fas fa-chevron-right color-vineta1"}),_c('p',{staticClass:"mb-0"},[_vm._v("Charlas o talleres como regalos. Se puede dar una tarjeta de regalo para participar de manera gratuita o con un descuento especial en un próximo evento de la compañía.")])]),_c('li',[_c('i',{staticClass:"fas fa-chevron-right color-vineta1"}),_c('p',{staticClass:"mb-0"},[_vm._v("Regalos virtuales. Incluyen tarjetas de regalo con descuentos en productos o servicios de la marca comprados en línea, acceso a cursos, descarga de material o libros digitales.")])]),_c('li',[_c('i',{staticClass:"fas fa-chevron-right color-vineta1"}),_c('p',{staticClass:"mb-0"},[_vm._v("Bolsas ecológicas. Pueden ser utilizadas tanto dentro del evento para guardar elementos del cliente, como por fuera del mismo, donde serán vistas po